<script>
import Auth from './Auth'
import { mapActions } from 'vuex'
import { emailRE } from '../../utils'

export default {
  components: {
    Auth
  },
  data () {
    return {
      error: '',
      emailSent: false
    }
  },
  methods: {
    ...mapActions(['forgetPassword', 'loading', 'loadingDone']),
    formSubmit (e) {
      const email = e.target.email.value
      if (!emailRE.test(email)) {
        this.error = ''
        return
      }
      this.loading()
      this.forgetPassword(email).then(() => {
        this.loadingDone()
        this.emailSent = true
      }).catch(err => {
        if (err.status === 404) {
          this.error = ''
        }
        this.loadingDone()
      })
    }
  },
  mounted () {
    document.title = ' - '
  }
}

</script>

<template>
<auth>
  <span slot="title"></span>
  <div slot="content">
    <form v-if="!emailSent" action="" @submit.prevent="formSubmit" method="post">
      <div class="form-group">
        <input class="form-control input-lg" type="text" name="email" value="" placeholder=""/>        
      </div>
      <p class="auth-error bg-danger" v-show="!!error">{{error}}</p>
      <div class="form-group">
        <input type="submit" value="" class="btn btn-primary btn-lg form-control input-lg" />  
      </div>
    </form>
    <p class="auth-info bg-info" v-if="emailSent">
      
    </p>
  </div>
  <p slot="extra">
    
    <router-link to="/login"></router-link>
  </p>
</auth>
</template>


