<script>
import Auth from './Auth'
import { mapActions } from 'vuex'

export default {
  components: {
    Auth
  },
  data () {
    return {
      error: '',
      success: false
    }
  },
  methods: {
    ...mapActions(['resetPassword', 'loading', 'loadingDone']),
    formSubmit (e) {
      const password = e.target.new_password.value
      const passwordConfirmation = e.target.password_confirmation.value
      const token = this.$route.params.token
      this.error = ''
      if (password.length < 6) {
        this.error = '6'
        return
      }
      if (password !== passwordConfirmation) {
        this.error = ''
        return
      }
      this.loading()
      this.resetPassword([token, password]).then(() => {
        this.loadingDone()
        this.success = true
      }).catch(res => {
        this.loadingDone()
        this.error = res.error
      })
    }
  },
  mounted () {
    document.title = ' - '
  }
}

</script>

<template>
<auth>
  <span slot="title"></span>
  <div slot="content">
    <form v-if="!success" action="" method="post" @submit.prevent="formSubmit">
      <div class="form-group">
        <input class="form-control input-lg" type="password" name="new_password" placeholder=""/>
      </div> 
      <div class="form-group">
        <input class="form-control input-lg" type="password" name="password_confirmation" placeholder=""/>
      </div>

      <p class="auth-error bg-danger" v-show="!!error">{{error}}</p>

      <div class="form-group">
        <input type="submit" value="" class="btn btn-primary btn-lg form-control input-lg" />  
      </div>
    </form>
    <p class="auth-info bg-info" v-if="success">
      <a href="/"></a>
    </p>
  </div>
  <p v-if="!success" slot="extra">
    
    <router-link to="/login"></router-link>
  </p>
</auth>
</template>


