<script>
import StatsNav from './StatsNav'

export default {
  components: {
    StatsNav
  },
  props: ['report'],
  filters: {
    percentage (val) {
      return (Math.round(val * 10) / 10) + '%'
    }
  },
  computed: {
    goalData () {
      return this.report[1]
    },
    notGoalData () {
      return this.report[0]
    }
  }
}
</script>

<template>
  <div>
    <stats-nav title=""></stats-nav>
    <div class="content-body">
      <h4></h4>
      <table v-if="goalData && goalData.length > 0" class="report table table-bordered table-hover">
        <thead>
          <tr>
            <th width="120px"></th>
            <th></th>
            <th width="120px"></th>
            <th width="120px"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="goal in goalData">
            <td>{{goal.goal_type}}</td>
            <td>{{goal.goal_desc}}</td>
            <td>{{goal.goals}}</td>
            <td>{{goal.goals_percent | percentage}}</td>
          </tr>
        </tbody>
      </table>
      <p v-else class="text-warning"></p>

      <template v-if="notGoalData && notGoalData.length > 0">
        <h4><span class="title-remark">()</span></h4>
        <table class="report table table-bordered table-hover">
          <thead>
            <tr>
              <th width="120px"></th>
              <th></th>
              <th width="120px"></th>
              <th width="120px"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="goal in notGoalData">
              <td>{{goal.goal_type}}</td>
              <td>{{goal.goal_desc}}</td>
              <td>{{goal.goals}}</td>
              <td>{{goal.goals_percent | percentage}}</td>
            </tr>
          </tbody>
        </table>
      </template>
    </div>
  </div>
</template>


