<script>
import Auth from './Auth'
import { mapActions } from 'vuex'
import { emailRE } from '../../utils'

export default {
  components: {
    Auth
  },
  data () {
    return {
      error: '',
      token: this.$route.query.token || '',
      invitedEmail: this.$route.query.email || ''
    }
  },
  methods: {
    ...mapActions(['register', 'loading', 'loadingDone']),
    formSubmit (e) {
      const name = e.target.name.value
      const email = e.target.email.value
      const password = e.target.password.value
      // const passwordConfirmation = e.target.password_confirmation.value

      this.error = ''

      if (!name) {
        this.error = ''
        return
      }
      if (name.length > 20) {
        this.error = '20'
        return
      }
      // const emailPattern = /^[A-Za-z0-9]+([-_.][A-Za-z0-9]+)*@([A-Za-z0-9]+[-.])+[A-Za-z0-9]{2,5}$/
      if (!emailRE.test(email)) {
        this.error = ''
        return
      }
      if (password.length < 6) {
        this.error = '6'
        return
      }
      // if (password !== passwordConfirmation) {
      //   this.error = ''
      //   return
      // }
      const user = {
        name: name,
        email: email,
        password: password,
        i: this.token
      }
      this.loading()
      this.register([user, () => {
        this.loadingDone()
        this.$router.replace('/')
      }, (response) => {
        this.loadingDone()
        if (response.status === 422) {
          this.error = ''
          return
        } else {
          this.error = ''
          return
        }
      }])
    }
  },
  mounted () {
    document.title = ' - '
  }
}

</script>

<template>
<auth>
  <span slot="title"></span>
  <div slot="content">
    <form action="" @submit.prevent="formSubmit" method="post">
      <div class="form-group">
        <input class="form-control input-lg" type="text" name="name" placeholder=""/>        
      </div>
      <div class="form-group">
        <input class="form-control input-lg" type="text" name="email" :value="invitedEmail" placeholder="" :disabled="!!invitedEmail"/>
      </div>
      <div class="form-group">
        <input class="form-control input-lg" type="password" name="password" placeholder=""/>
      </div> 
      <!-- <div class="form-group">
        <input class="form-control input-lg" type="password" name="password_confirmation" placeholder=""/>
      </div> -->
      
      <p class="auth-error bg-danger" v-show="!!error">{{error}}</p>

      <div class="form-group">
          <input type="submit" value="" class="btn btn-primary btn-lg form-control input-lg" />  
      </div>
    </form>
  </div>
  <p slot="extra">
    
    <router-link to="/login"></router-link>
  </p>
</auth>
</template>


